import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/dist/query/react";
import { serverUrl } from "../../utlis/config";

const orderProductApi = createApi({
  reducerPath: "orderProductApi",
  // 
  baseQuery: fetchBaseQuery({
    baseUrl: `${serverUrl}/api/`,
    //
    prepareHeaders: (headers, { getState }) => {
      //  token
      const token = getState().auth.token;
      if (!token) return;
      headers.set("Authorization", `Bearer ${token}`);
      return headers;
    },
  }),
  tagTypes: ["orderProduct"], //  Api 
  endpoints(build) {
    return {
      getOrderProducts: build.query({
        query() {
          return {
            url: `orders?populate=*`,
          };
        },
        // transformResponse 
        transformResponse(baseQueryReturnValue) {
          return {
            data: baseQueryReturnValue.data,
            meta: baseQueryReturnValue.meta.pagination,
          };
          // App.js  useGetordersQuery()  data
        },
        providesTags: [{ type: "orderProduct", id: "LIST" }], // getorders'orders'
      }),
      addOrderProduct: build.mutation({
        query(product) {
          return {
            url: "order-products",
            method: "post",
            body: { data: product },
          };
        },
        invalidatesTags: [{ type: "orderProduct", id: "LIST" }],
        // addorder'orders'getorders'orders'
      }),
    };
  },
});

export const { useGetOrderProductsQuery, useAddOrderProductMutation } =
  orderProductApi;

export default orderProductApi;
