import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/dist/query/react";
import { serverUrl } from "../../utlis/config";

const orderApi = createApi({
  reducerPath: "orderApi",
  // 
  baseQuery: fetchBaseQuery({
    baseUrl: `${serverUrl}/api/`,
    //
    prepareHeaders: (headers, { getState }) => {
      //  token
      const token = getState().auth.token;
      if (!token) return;
      headers.set("Authorization", `Bearer ${token}`);
      return headers;
    },
  }),
  tagTypes: ["order"], //  Api 
  endpoints(build) {
    return {
      // get 
      getOrders: build.query({
        query() {
          return {
            // ?populate=*  api image 
            url: `orders?populate=*`,
          };
        },
        // transformResponse 
        transformResponse(baseQueryReturnValue) {
          return {
            data: baseQueryReturnValue.data,
            meta: baseQueryReturnValue.meta.pagination,
          };
          // App.js  useGetordersQuery()  data
        },
        providesTags: [{ type: "order", id: "LIST" }], // getorders'orders'
      }),
      // post 
      addOrder: build.mutation({
        query(order) {
          return {
            url: "orders",
            method: "post",
            body: { data: order },
          };
        },
        invalidatesTags: [{ type: "order", id: "LIST" }],
        // addorder'orders'getorders'orders'
      }),
    };
  },
});

export const { useGetOrdersQuery, useAddOrderMutation } = orderApi;

export default orderApi;
