import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";

export default function useValidation(validateFuntion) {
  //   console.log(input);
  const dispatch = useDispatch();

  const [inputValue, setInputValue] = useState("");
  const [isTouched, setIsTouched] = useState(false);

  // validateFuntion  Boolean
  const isValid = validateFuntion(inputValue);
  //  isValid  false!false === trueinput.isTouched  onBlur  true
  const isError = !isValid && isTouched; //  isValid  false isError  true

  const onChangeValue = (e) => {
    e.preventDefault();
    setInputValue(e.target.value);
    // dispatch(onInput(e.target.value));
  };
  const onBlurValue = () => {
    setIsTouched(true);
    // dispatch(onBlur());
  };
  const onReset = () => {
    setInputValue("");
    setIsTouched(false);
    // dispatch(reset());
  };

  return {
    value: inputValue,
    isValid,
    isError,
    onChangeValue,
    onBlurValue,
    onReset,
  };
}
