import { createApi, fetchBaseQuery } from "@reduxjs/toolkit/dist/query/react";
import { serverUrl } from "../../utlis/config";

const productsApi = createApi({
  reducerPath: "productsApi",
  // 
  baseQuery: fetchBaseQuery({
    baseUrl: `${serverUrl}/api/`,
  }),
  tagTypes: ["products"], //  Api 
  endpoints(build) {
    return {
      // get 
      getProducts: build.query({
        query() {
          return {
            // ?populate=*  api image 
            url: "products?populate=*",
          };
        },
        // transformResponse 
        transformResponse(baseQueryReturnValue) {
          return {
            data: baseQueryReturnValue.data,
            meta: baseQueryReturnValue.meta.pagination,
          };
          // App.js  useGetProductsQuery()  data
        },
        providesTags: [{ type: "products", id: "LIST" }], // getProducts'products'
      }),
      //  id get 
      getProductById: build.query({
        query(id) {
          return `products/${id}?populate=*`;
        }, // transformResponse 
        transformResponse(baseQueryReturnValue) {
          return baseQueryReturnValue.data;
          // Form  useGetProductsQuery()  data
        },
        keepUnusedDataFor: 60, // 60s
        providesTags: (result, error, id) => [{ type: "products", id }],
      }),
      // delete 
      delProduct: build.mutation({
        query(id) {
          return {
            url: `products/${id}`,
            method: "delete",
          };
        }, // transformResponse 
        transformResponse(baseQueryReturnValue) {
          return baseQueryReturnValue.data;
        },
        invalidatesTags: ["products"],
      }),
      // post 
      addProduct: build.mutation({
        query(product) {
          return {
            url: "products",
            method: "post",
            body: { data: product },
          };
        },
        invalidatesTags: [{ type: "products", id: "LIST" }],
        // addProduct'products'getProducts'products'
      }),
      // put 
      updateProduct: build.mutation({
        query(product) {
          return {
            url: `products/${product.id}`,
            method: "put",
            body: { data: product.attributes },
          };
        },
        invalidatesTags: (result, error, id) => [
          { type: "products", id },
          { type: "products", id: "LIST" },
        ],
      }),
    };
  },
});

export const {
  useGetProductsQuery,
  useGetProductByIdQuery,
  useDelProductMutation,
  useAddProductMutation,
  useUpdateProductMutation,
} = productsApi;

export default productsApi;
